import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { DatePipe } from '@angular/common';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  providers: [DatePipe]
})
export class AppComponent {
  title = 'dashBord-task';
  public items: any[];
  currentDate: any = new Date();

  constructor(private router: Router,private  datePipe: DatePipe) {
      this.items = this.mapItems(router.config);
      this.currentDate = this.datePipe.transform(this.currentDate, 'dd MMM yyyy');

  }

  public onSelect({ item }): void {
      if (!item.items) {
          this.router.navigate([ item.path ]);
      }
  }

  private mapItems(routes: any[], path?: string): any[] {
      return routes.map(item => {
          const result: any = {
              text: item.text,
              path: (path ? `${ path }/` : '') + item.path
          };

          if (item.children) {
              result.items = this.mapItems(item.children, item.path);
          }

          return result;
      });
  }

  public userMenuData: any[] = [{
    text: 'Ahmed Mohamed Adel',
    items: [{ text: 'Profile' }, { text: 'Logout' }]
}]
}
