import { Component } from '@angular/core';
import { employeesObj } from './employeesData';
import { GridDataResult, PageChangeEvent } from '@progress/kendo-angular-grid';
import { EmpsMandatryDateComponent } from '../emps-mandatry-date/emps-mandatry-date.component';
EmpsMandatryDateComponent
@Component({
    selector: 'search-emps',
    templateUrl: 'search-emps.component.html',
    styleUrls: ['search-emps.component.css']
})
export class SearchEmpsComponent {
    public gridView: any;
    gridData:any[]=employeesObj;
    searchResultDate:any[];
    public mySelection: number[] = [];
    public pageSize =8;
    public skip = 0;
    public departmantsList: Array<string> = [
        'it', 'ui', 'back-end', 'hr'
        ];

constructor() {
    this.loadItems(this.gridData);
}

public pageChange(event: PageChangeEvent): void {
    this.skip = event.skip;
    this.loadItems(this.gridData);
}

private loadItems(datArray): void {
    this.gridView = {
        data: datArray.slice(this.skip, this.skip + this.pageSize),
        total: datArray.length
    };
}


filterEmpsGrid(EmpName,findName,department,findDepartment){
    debugger
if(findName&&!findDepartment){
    this.searchResultDate=employeesObj.filter(x => x.name.toLowerCase().includes(EmpName.toLowerCase()))  
    
}else if(!findName && findDepartment){
    this.searchResultDate=employeesObj.filter(x => x.Department.toLowerCase().includes(department.toLowerCase()))  
}
else{
    this.searchResultDate=employeesObj.filter(x => x.name.toLowerCase().includes(EmpName.toLowerCase())||x.Department.toLowerCase().includes(department.toLowerCase()))  
}
this.loadItems( this.searchResultDate);

}


removeFilter(nameInput,findName,department,findDepartment){
if(findName){
    nameInput.reset();  
}
if(findDepartment){
    department.reset();
}
    this.gridData=employeesObj;
    this.loadItems( this.gridData);
}
}
